	getById(id: string | undefined) {
		if (!this.initialized)
			this.init();

		console.log("getById");
		if (!id)
			return { success: false, item: null };

		let article = this.list.find((x: any) => { return x.id === id });
		let result;

		if (article)
			result = { success: true, item: article };
		else
			result = { success: false, item: article };

		return result;
	}

